<template>
  <label class="input__label" :class="_class || ''" :for="name">
    <span class="input__span" :class="required ? 'required' : ''">
      <label v-if="required" style="margin: 0">*</label>
      {{ inputplaceholder }}
    </span>
    <textarea
      :name="name"
      v-if="type === 'textarea'"
      class="input__input"
      placeholder=" "
      :required="required || false"
      :rows="rows"
      :maxLength="maxLength"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      :style="style"
    />

    <slot v-else-if="type === 'select'"> </slot>

    <input
      :name="name"
      class="input__input"
      v-else-if="type === 'tel'"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      v-maska="'+7(###)-###-####'"
      maskChar=" "
      :placeholder="placeholder || ' '"
    />
     <input
      :name="name"
      class="input__input"
      v-else-if="type === 'digits'"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      v-maska="'####################################'"
      maskChar=" "
      :placeholder="placeholder || ' '"
    />
    <input
      :name="name"
      v-else
      class="input__input"
      :placeholder="placeholder || ' '"
      :required="required || false"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
    />
  </label>
</template>

<script>
export default {
  props: [
    "type",
    "_classname",
    "required",
    "inputplaceholder",
    "text",
    "placeholder",
    "val",
    "rows",
    "maxLength",
    "modelValue",
    "style",
    "options",
    "name",
    '_class'
  ],
  data() {
    return {
      select: "",
      inputValue: "val",
      inputText: "text",
      selectOptions: [],
    };
  },
  mounted() {
    if (this.$props.val && this.$props.text) {
      this.inputValue = this.$props.val;
      this.inputText = this.$props.text;
    }
    this.getOptions();
  },
  methods: {
    getOptions() {
      this.$props.options &&
        this.$props.options.map((v) => {
          this.selectOptions.push({
            value: v[this.inputValue],
            label: v[this.inputText],
          });
        });
    },
  },
};
</script>

<style>
.input__label {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.input__span {
  margin-bottom: 10px;
  width: 100%;
  display: inline-block;
}

.input__span.required label {
  color: red;
  font-family: GolosTextBold;
}

.input__input {
  box-shadow: 0px 0px 0px 2px rgba(0, 60, 160, 0.2);
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  font-size: 18px;
  font-family: GolosTextRegular;
  box-sizing: border-box;
  -webkit-appearance: none;
}

.input__label select {
  color: var(--blue_light);
  background: var(--white);
  max-width: 100%;
  overflow: hidden;
  overflow-wrap: break-word;
}

.minContent {
  width: min-content;
}
.input__input::placeholder {
  color: var(--blue_light);
  /* opacity: .8; */
}

.v-select {
  padding: 0;
  border: none;
  padding-right: 10px;
}

span.vs__selected + input.vs__selected,
span.vs__selected + input[type="search"] {
  display: none !important;
}

li.vs__dropdown-option {
  white-space: pre-wrap;
}

.v-select > div {
  border: none;
}

.v-select__placeholder, .vs__search::placeholder {
  color: var(--blue_light) !important;
}

.v-select__indicators,
.vs__actions {
  opacity: 0;
  display: none !important;
}

.v-select__single-value,
.v__select__value-container {
  margin-right: 10px;
}

span.vs__selected,
.vs__selected-options {
  /* white-space: nowrap; */
  overflow: hidden;
  text-overflow: ellipsis;
}

input:disabled {
  background: var(--light);
}
</style>
