<template>
  <div className="container" v-if="claims.length">
    <div className="claimsScreen__wrapper">
      <p className="claims__title">Архив обращений</p>
      <div className="claims__items">
        <div
          :key="index"
          v-for="(claim, index) in claims"
          className="claims__items-item"
        >
          <details>
            <summary> Обращение номер {{ claim.id }}</summary>
            <p>Тема: {{ claim.subject.text }}</p>
            <p>Статус: {{ claim.state.status }}</p>
            <p>
              Дата изменения статуса:
              {{
                claim.state.createdDate
                  ? new Date(claim.state.createdDate).toLocaleString("ru")
                  : "- нет данных -"
              }}
            </p>
            <p>
              Регистрационный номер:
              {{ claim.state.registrationNumber || "Не присвоен" }}
            </p>
            <p>
              Дата создания обращения:
              {{
                claim.createdDate
                  ? new Date(claim.createdDate).toLocaleString("ru")
                  : "- нет данных -"
              }}
            </p>
          </details>
        </div>
      </div>
      <Button
        v-if="canGoForward"
        class="regularBtn"
        @action="showMore"
        text="показать ещё"
      />
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Button from "../FormComponents/Button.vue";
export default {
  components: { Button },
  data() {
    return {
      limit: 20,
      offset: 0,
      canGoForward: false,
      claims: [],
    };
  },
  mounted() {
    const { limit, offset } = this;
    this.get_claims({ limit, offset }).then((res) => {
      this.processClaims(res);
    });
  },
  methods: {
    ...mapActions(["get_claims"]),
    showMore() {
      this.get_claims(20, this.offset).then((res) => {
        this.processClaims(res);
      });
    },
    processClaims(res) {
      this.claims = [...this.claims, ...res];
      if (res.length >= 20) {
        this.offset = this.offset + 20;
        this.canGoForward = true;
      } else {
        this.canGoForward = false;
      }
    },
  },
};
</script>

