<template>
  <div class="formScreen__wrapper">
    <div class="formScreen__content">
      <div class="form">
        <form @submit="onFormSumbit">
          <small class="input__span required"
            ><label>*</label> — поля, обязательные для заполнения</small
          >
          <p class="form__title">Адресат</p>
          <form-input
            type="select"
            name="registrationPlace.id"
            inputplaceholder="Адресат"
            :required="true"
          >
            <v-select
              :options="regPlace"
              class="input__input form__select"
              :required="true"
              placeholder="Выберите, куда Вы хотите обратиться"
              :reduce="(option) => option.id"
              label="name"
              v-model="formState.registrationPlace.id"
            >
              <template #no-options="">
                Нет результатов
              </template>
            </v-select>
          </form-input>
          <br />
          <br />
          <p class="form__title">Обращение</p>
          <form-input
            type="select"
            name="type"
            inputplaceholder="Вид обращения"
            class="form__select"
            :required="true"
          >
            <v-select
              :searchable="false"
              :options="types"
              class="input__input form__select"
              :required="true"
              placeholder="Выберите вид обращения"
              :reduce="(option) => option.val"
              label="text"
              v-model="formState.type"
            >
              <template #no-options="">
                Нет результатов
              </template>
            </v-select>
          </form-input>
          <FormInput
            type="textarea"
            rows="15"
            style="resize: none;"
            name="text"
            v-model="formState.text"
            :required="true"
            inputplaceholder="Содержание обращения"
            _class="formInput"
            maxLength="10000"
          />
          <p style="text-align: right">
            <small class="form__small"
              >{{ formState.text.length }} / 10000</small
            >
          </p>
          <small class="form__small"
            >Поле текста обращения имеет техническое ограничение в 10 000
            символов (с пробелами). Если размер вашего обращения превышает
            указанное ограничение, прикрепите его в виде отдельного файла, а в
            текстовом поле кратко укажите суть обращения.</small
          >
          <div class="form__files">
            <p>Приложения к обращению</p>
            <input
              type="file"
              multiple
              @change="onFilesInputChange"
              ref="fileInput"
              :disabled="formState.person.files.length === 5"
              accept=".txt, .doc, .docx, .rtf, .xls, .xlsx, .pdf, image/gif, image/jpeg, image/jpg, image/png, image/bmp, .tiff, .psp"
            />
            <p @click="this.$refs.fileInput.click()" class="form__button">
              Прикрепить файл
            </p>
            <div class="files__list">
              <div v-for="(file, idx) in formState.person.files" :key="idx">
                <p>
                  {{ file.file.name }}
                  <Button
                    type="button"
                    text="✖"
                    class="delete__button"
                    @click="handleFileDelete(idx)"
                  />
                </p>
              </div>
            </div>
            <p>
              <small>
                Максимально допустимое количество вложений 5 файлов. Суммарный
                объем файлов не может превышать 20 Мбайт. Допустимы следующие
                форматы файлов: txt, doc, docx, rtf, xls, xlsx, pdf, jpeg, jpg,
                png, bmp, tiff, psp.
              </small>
            </p>
          </div>

          <p class="form__title">Личные данные</p>
          <div class="name_surname_pantonymic__wrapper">
            <FormInput
              type="text"
              inputplaceholder="Фамилия"
              :required="true"
              v-model="formState.person.lastName"
            />
            <FormInput
              type="text"
              inputplaceholder="Имя"
              :required="true"
              v-model="formState.person.firstName"
            />
            <FormInput
              type="text"
              inputplaceholder="Отчество"
              v-model="formState.person.middleName"
            />
          </div>
          <div class="email_confirm__wrapper">
            <FormInput
              type="email"
              inputplaceholder="Адрес электронной почты"
              :required="true"
              v-model="formState.person.email"
              placeholder="example@mail.ru"
            />
            <FormInput
              type="email"
              inputplaceholder="Подтвердите адрес электронной почты"
              :required="true"
              v-model="formState.person.email_confirm"
              placeholder="example@mail.ru"
              _class="minContent"
            />
          </div>
          <small class="form__small"
            >Ответ на Ваше обращение будет направлен в форме электронного
            документа по указанному адресу электронной почты.</small
          >
          <div class="tel__wrapper">
            <FormInput
              type="tel"
              inputplaceholder="Контактный телефон"
              v-model="formState.person.phone"
              placeholder="+7(___)-___-____"
            />
          </div>
          <br />
          <br />
          <div v-if="user.person.fullName === ''">
            <vue-recaptcha
              id="formCaptcha"
              siteKey="6LfbLe8ZAAAAADQwTtuHq6moxMtiIM_vivpkC8RW"
              size="normal"
              :show="1"
              theme="light"
              @verify="recaptchaVerified"
              @expire="recaptchaExpired"
              @fail="recaptchaFailed"
              ref="vueRecaptcha"
            >
            </vue-recaptcha>
          </div>

          <br /><br />
          <label>
            Персональные данные, сообщаемые пользователями в обращении, хранятся
            и обрабатываются в соответствии с законодательством РФ.
          </label>
          <br /><br />
          <Button
            type="submit"
            class="formSubmitButton"
            text="Отправить обращение"
            :disabled="
              formState.person.email !== formState.person.email_confirm ||
                !captcha
            "
          />
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "../FormComponents/Button";
import FormInput from "../FormComponents/FormInput";
import { types } from "@/utils/constants.js";
import { mapActions, mapGetters } from "vuex";
import { trimFields, removeEmpty } from "@/utils/functions";
export default {
  data() {
    return {
      result: {},
      params: {},
      regPlace: [],
      types,
      captcha: false, //def - false
      formState: {
        familiarFilingInfo: true,
        processingConsent: true,
        registrationPlace: {
          id: "",
        },
        text: "",
        type: "",
        person: {
          firstName: "",
          lastName: "",
          middleName: "",
          email: "",
          email_confirm: "",
          type: "FL",
          phone: "",
          files: [],
          benefitGroup: "",
        },
      },
    };
  },
  methods: {
    onFormSumbit(e) {
      e.preventDefault();
      let files = [...this.formState.person.files];
      let object = JSON.parse(JSON.stringify({ ...this.formState }));
      if (files.length !== 0) delete object.person["files"];
      removeEmpty(object);
      delete object.person.email_confirm;
      delete object.person.fullName;
      trimFields(object);
      this.save({ object, files })
        .then((res) => {
          console.log(res);
          this.result = {
            ...this.result,
            ...res,
          };
        })
        .catch(() => {
          this.result = {
            ...this.result,
            success: false,
          };
        })
        .finally(() => {
          this.$router.push({
            name: "result",
            params: { result: JSON.stringify(this.result) },
          });
        });
    },
    ...mapActions(["get_registration_place", "save"]),
    recaptchaVerified(response) {
      if (response) {
        this.captcha = true;
      }
    },
    recaptchaExpired() {
      this.$refs.vueRecaptcha.reset();
    },
    recaptchaFailed() {},
    onFilesInputChange(e) {
      let size = 0;
      let files = [...e.target.files];
      for (let file in files) {
        size += files[file].size / (1024 * 1024);
      }

      if (files.length > 5) {
        alert(
          "Вы пытаетесь загрузить больше 5 файлов. Файлы не будут загружены."
        );
        return "";
      }

      if (size > 20) {
        alert(
          "Суммарный объём загружаемых файлов превышает 20МБ. Файлы не будут загружены."
        );
        return "";
      }

      this.formState.person.files = [
        ...this.formState.person.files,
        [...e.target.files].map((file) => {
          return {
            file,
          };
        }),
      ].flat();
    },
    handleFileDelete(idx) {
      this.formState.person.files.splice(idx, 1);
    },
  },
  mounted() {
    this.get_registration_place().then((res) => (this.regPlace = res.data));
    let params = new URL(document.location).searchParams;
    this.params = {
      ...this.params,
      type: params.get("type"),
      to: params.get("to"),
    };

    setTimeout(() => {
      if (this.user.person.firstName !== "") {
        this.captcha = true;
        let email = "";
        let phone = "";
        if (this.user.contacts && this.user.contacts.length !== 0) {
          this.user.contacts.forEach((val) => {
            if (val.type === "EML") {
              email = val.value;
            }
            if (val.type === "MBT") {
              phone = val.value;
            }
          });
        }
        phone =
          phone.substring(0, 7) +
          "-" +
          phone.substring(7, 10) +
          "-" +
          phone.substring(10, 14);
        this.formState.person = {
          ...this.formState.person,
          ...this.user.person,
          email: email,
          email_confirm: email,
          phone: phone,
        };
      }
    }, 500);
  },
  components: { Button, FormInput },
  computed: {
    ...mapGetters(["user"]),
  },
  watch: {
    params(value) {
      if (value.to) {
        this.formState.registrationPlace.id = Number(value.to);
      }
      if (value.type) {
        this.formState.type = value.type;
      }
    },
    "formState.registrationPlace.id"(value) {
      let type = this.params.type || null;
      let string = `form?to=${value}${type ? "&type=" + type : ""}`;
      this.params.to = value;
      window.history.pushState("object or string", "Title", string);
    },
    "formState.type"(value) {
      let to = this.params.to || null;
      let string = `form?${to ? "to=" + to + "&" : ""}type=${value}`;
      this.params.type = value;
      window.history.pushState("object or string", "Title", string);
    },
    // user(v) {
    //   // console.log(v);
    // },
    // formState: {
    //   deep: true,
    //   handler(val) {
    //     // console.log(val);
    //   },
    // },
  },
};
</script>

