<template>
  <layout>
    <router-view></router-view>
  </layout>
</template>

<script>
import Layout from "@/components/Layout";
import { mapActions } from "vuex";
export default {
  components: { Layout },
  methods: {
    ...mapActions(["auth_continue"]),
  },
  mounted() {
    if (window.location.pathname.includes("/continue")) {
      let code = window.location.search.split("=")[1].split("&")[0] || "";
      if (code) {
        this.auth_continue({code});
      }
    }
  },
};
</script>
