<template>
  <div class="ResultScreen__wrapper">
    <div class="ResultScreen__content">
      <div class="form">
        <div class="result__content" v-if="sendResult.success">
          <h3 class="searchResultHeader">Результат</h3>
          <p>
            Дата подачи обращения:
            {{ new Date(sendResult?.data.createdDate).toLocaleString("ru") }}
          </p>
          <div class="status__wrapper">
            <div class="status__wrapper-col">
              <div>
                <h3
                  v-text="'Обращение отправлено'"
                  class="status__wrapper-row__header success"
                ></h3>
                <p v-text="'Статус'" class="status__wrapper-row__text"></p>
              </div>
              <div>
                <h3 class="status__wrapper-row__header">
                  {{ sendResult?.data?.person?.email }}
                </h3>
                <p
                  v-text="'Ответ направлен на email'"
                  class="status__wrapper-row__text"
                ></p>
              </div>
            </div>
            <div class="status__wrapper-col">
              <div>
                <h3 class="status__wrapper-row__header">
                  {{ sendResult?.data?.person?.lastName }}
                </h3>
                <p
                  v-text="'Фамилия заявителя'"
                  class="status__wrapper-row__text"
                ></p>
              </div>
              <div>
                <h3 class="status__wrapper-row__header">
                  {{ sendResult?.data.id }}
                </h3>
                <p
                  v-text="'Номер обращения для проверки'"
                  class="status__wrapper-row__text"
                ></p>
              </div>
            </div>
          </div>
        </div>

        <form v-else @submit="onFormSumbit">
          <small class="input__span required"
            ><label>*</label> — поля, обязательные для заполнения</small
          >
          <div class="resultForm__input_wrapper">
            <FormInput
              name="number"
              type="digits"
              placeholder="Введите номер обращения"
              inputplaceholder="Идентификационный номер обращения"
              v-model="check.id"
              :required="true"
            />
            <small
              >Номер обращения был направлен на Вашу электронную почту при
              отправке обращения через интернет-приемную.</small
            >
          </div>
          <div class="resultForm__input_wrapper">
            <FormInput
              name="surname"
              type="text"
              placeholder="Введите фамилию заявителя"
              inputplaceholder="Фамилия заявителя"
              v-model="check.lastName"
              :required="true"
              maxLength="20"
            />
            <small
              >Введите фамилию в той раскладке (русской или английской), как
              было указано Вами при отправке обращения.</small
            >
          </div>
          <div v-if="user.person.fullName === ''">
            <vue-recaptcha
              id="resultCaptcha"
              siteKey="6LfbLe8ZAAAAADQwTtuHq6moxMtiIM_vivpkC8RW"
              size="normal"
              :show="1"
              theme="light"
              @verify="recaptchaVerified"
              @expire="recaptchaExpired"
              @fail="recaptchaFailed"
              ref="vueRecaptcha"
            >
            </vue-recaptcha>
          </div>
          <br/>
          <br/>
          <Button
            type="submit"
            class="formSubmitButton"
            text="Далее"
            :disabled="!captcha"
          />
          <div
            v-if="checkResult.success && checkResult.data.status"
            class="resultForm__input_wrapper"
            id="resultForm__input_wrapper"
            ref="scrollTo"
          >
            <h2 class="searchResultHeader">Результат поиска</h2>
            <h3 class="searchResultHeader">Обращение {{ num }}</h3>
            <p>Идентификационный номер</p>
            <div class="status__wrapper">
              <div class="status__wrapper-col">
                <div>
                  <h3 class="status__wrapper-row__header success">
                    {{
                      checkResult.data && checkResult.data.status
                        ? checkResult.data.status
                        : "Ожидает регистрации"
                    }}
                  </h3>
                  <p class="status__wrapper-row__text">Статус</p>
                </div>
                <div>
                  <h3 class="status__wrapper-row__header">
                    {{
                      checkResult.data && checkResult.data.registrationNumber
                        ? checkResult.data.registrationNumber
                        : "Не присвоен"
                    }}
                  </h3>
                  <p class="status__wrapper-row__text">Регистрационный номер</p>
                </div>
              </div>
              <div class="status__wrapper-col">
                <div>
                  <h3 class="status__wrapper-row__header">
                    {{
                      checkResult.data && checkResult.data.createdDate
                        ? new Date(checkResult.data.createdDate).toLocaleString(
                            "ru"
                          )
                        : "—"
                    }}
                  </h3>
                  <p class="status__wrapper-row__text">Дата подачи обращения</p>
                </div>
                <div>
                  <h3 class="status__wrapper-row__header">
                    {{
                      checkResult.data && checkResult.data.registrationDate
                        ? new Date(checkResult.data.registrationDate)
                            .toLocaleString("ru")
                            .split(",")[0]
                        : "—"
                    }}
                  </h3>
                  <p v-text="'Дата регистрации обращения'" class="status__wrapper-row__text">
                    
                  </p>
                </div>
              </div>
            </div>
            <small>
              Ответ на Ваше обращение был направлен на указанный адрес
              электронной почты.
            </small>
          </div>
          <div
            v-if="!checkResult.success && showCheckResultErrorMsg"
            class="result__content result__error fullwidth"
            :style="{ display: !checkResult.success ? 'block' : 'none' }"
          >
            <FormInput
              name="status"
              type="text"
              v-model="checkResult.errorMessage"
              disabled
              _class="result__status fail "
            />
          </div>
          <div
            v-if="sendResult.showErrorMsg"
            class="result__content result__error fullwidth"
            :style="{ display: !sendResult.success ? 'block' : 'none' }"
          >
            <FormInput
              name="status"
              type="text"
              v-model="sendResult.errorMessage"
              :disabled="true"
              _class="result__status fail"
            />
          </div>
        </form>
      </div>
    </div>
  </div>
  <ClaimsList />
</template>

<script>
import FormInput from "../FormComponents/FormInput";
import Button from "../FormComponents/Button";
import { mapActions, mapGetters } from "vuex";
import ClaimsList from '../ClaimsList';
export default {
  components: { FormInput, Button, ClaimsList },
  props: ["result"],
  data() {
    return {
      showCheckResultErrorMsg: false,
      num: "",
      captcha: false,
      check: {
        id: "",
        lastName: "",
      },
      checkResult: {
        data: {
          status: "",
          registrationNumber: "",
          createdDate: "",
          registrationDate: "",
        },
        errorMessage: "",
        success: true,
      },
      sendResult: {
        data: {
          id: "",
          createdDate: "",
          person: {
            email: "",
            lastName: "",
          },
        },
        errorMessage: "Ошибка отправки обращения",
        success: false,
        showErrorMsg: false,
      },
    };
  },
  mounted() {
    setTimeout(() => {
      if (this.user.person.firstName !== "") {
        this.captcha = true;
        this.check.lastName = this.user.person.lastName;
      }
    }, 500);

    if (this.result) {
      let res = JSON.parse(this.result);
      if (!res.success) this.sendResult.showErrorMsg = true;
      this.sendResult = {
        ...this.sendResult,
        ...res
      };
    }
  },
  watch: {
    // sendResult: {
    //   deep: true,
    //   handler(v) {
    //     console.log(v);
    //   },
    // },
    // "checkResult.success"(value) {
    //   console.log(value);
    // },
    showCheckResultErrorMsg(value) {
      if(value) {
        setTimeout(() => {
          this.showCheckResultErrorMsg = false
        }, 3000)
      }
    }
  },
  methods: {
    recaptchaVerified(response) {
      if (response) {
        this.captcha = true;
      }
    },
    recaptchaExpired() {
      this.$refs.vueRecaptcha.reset();
    },
    ...mapActions(["get_by_id_and_last_name"]),
    recaptchaFailed() {},
    onFormSumbit(e) {
      e.preventDefault();
      let { id, lastName } = this.check;
      lastName = lastName.trim();
      this.num = id;
      this.get_by_id_and_last_name({ id, lastName }).then((res) => {
        this.checkResult = {
          ...this.checkResult,
          ...res,
        };

        if (res.success) {
          setTimeout(() => {
            this.$refs.scrollTo.scrollIntoView({ behavior: "smooth" });
          }, 500);
        } else {
          this.showCheckResultErrorMsg = true
        }
      });
    },
  },
  computed: {
    ...mapGetters(["user"]),
  },
};
</script>

