export const resolvePath = (object, path, defaultValue) => path.split('.').reduce((o, p) => o ? o[p] : defaultValue, object)

export const setPath = (object, path, value) => path.split('.').reduce((o, p, i) => o[p] = path.split('.').length === ++i ? value : o[p] || {}, object)

export const removeEmpty = obj => {
    Object.keys(obj).forEach(key => {
        if (obj[key] && typeof obj[key] === 'object') removeEmpty(obj[key]);
        if (typeof obj[key] === 'object' && Object.keys(obj[key]).length === 0) delete obj[key]
        else if (obj[key] === null || obj[key] === '' || obj[key] === 'undefined') delete obj[key];
    });
};

export const buildFormData = (formData, data, parentKey) => {
    if (data && typeof data === 'object' && !(data instanceof Date) && !(data instanceof File)) {
        Object.keys(data).forEach(key => {
            buildFormData(formData, data[key], parentKey ? `${parentKey}[${key}]` : key);
        });
    } else {
        const value = data == null ? '' : data;

        formData.append(parentKey, value);
    }
} 

export const trimFields = obj => {
    Object.keys(obj).forEach(key => {
        if (obj[key] && obj[key] !== null && typeof obj[key] === 'object') trimFields(obj[key]);
        if (typeof obj[key] === 'string') obj[key] = obj[key].trim()
    });
};