            :class="user.person.fullName ? 'user' : ''"
          >
            <img
              src="https://esia.gosuslugi.ru/idp/resources/img/flt/ru/logo.svg"
              alt="auth"
            />
          </div>
          <div class="commonContent__authtext">
            <p
              v-if="user.person.fullName === ''"
              class="commonContent__authtext-second"
            >
              Это удобно — поля обращения автоматически заполнятся вашими
              данными
            </p>
          </div>
          <p
            v-if="user.person.fullName"
            class="commonContent__authtext-first user"
          >
            {{ fullNameToLetters(user.person.fullName) }}
          </p>
          <Button
            v-if="user.person.fullName === ''"
            class="authBtn"
            text="Войти через Госуслуги"
            @action="auth"
          />
          <Button
            v-if="user.person.fullName"
            class="btn-link"
            text="Мои обращения"
            @action="openLink('/result')"
          />

          <p style="margin: 0" v-if="user.person.fullName">
            <Button class="btn-link" text="Выйти" @action="logout" />
          </p>
        </div>
      </div>
      <!-- form -->
      <div v-if="currentPath === '/form'" class="commonContent__formWrapper">
        <div class="commonContent__formTitle">
          <p>Направить обращение</p>
        </div>
        <div class="commonContent__formText">
          Для отправки обращения заполните поля, которые указаны ниже.
          <br />
          После отправки обращения на указанный электронный адрес будет <br />
          направлено сообщение с подтверждением получения Вашего обращения.
        </div>
      </div>
      <!-- result -->
      <div
        v-else-if="currentPath === '/result'"
        class="commonContent__resultWrapper"
      >
        <div class="commonContent__resultTitle">
          <p>Узнать статус обращения</p>
        </div>
        <div class="commonContent__resultText">
          Для проверки статуса обращения введите идентификационный номер
          обращения, а также фамилию заявителя.
        </div>
      </div>
      <!-- privacy -->
      <div
        v-else-if="currentPath === '/privacy'"
        className="commonContent__resultWrapper"
      >
        <div className="commonContent__resultTitle">
          <p>Условия предоставления информации</p>
        </div>
      </div>
      <!-- rules -->
      <div
        v-else-if="currentPath === '/rules'"
        className="commonContent__resultWrapper"
      >
        <div className="commonContent__resultTitle">
          <p>Порядок рассмотрения обращений граждан <br />в электронном виде</p>
        </div>
      </div>
      <!-- default -->
      <div v-else className="commonContent__mainWrapper">
        <div className="commonContent__mainWrapper-buttons">
          <Button
            text="Направить обращение"
            className="sendMessageBtn"
            @action="openLink('/form')"
          />
          <Button
            text="Узнать статус обращения"
            className="regularBtn"
            @action="openLink('/result')"
          />
        </div>
        <div className="commonContent__mainText">
          <p>
            Обращения рассматриваются в течение 30 дней со дня регистрации.
            <a href="/rules">
              <br />Порядок рассмотрения обращений граждан в электронном виде</a
            >
          </p>
        </div>
      </div>
    </div>
  </div>
  <Loader v-if="isLoading" />
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Button from "../FormComponents/Button";
import Loader from "../Loader"; 
export default {
  data() {
    return {
      currentPath: "/",
      page: window.location.pathname,
    };
  },
  mounted() {
    this.getCurrentPath(this.page);
    this.get_user();
  },
  components: { Button, Loader },
  methods: {
    getCurrentPath(page) {
      switch (page) {
        case "/form":
          this.currentPath = "/form";
          break;
        case "/result":
          this.currentPath = "/result";
          break;
        case "/privacy":
          this.currentPath = "/privacy";
          break;
        case "/rules":
          this.currentPath = "/rules";
          break;
        default:
          this.currentPath = "/";
          break;
      }
    },
    fullNameToLetters(fn) {
      fn = fn.split(" ");
      let str = fn[0];
      for (let i = 1; i < fn.length; i++) {
        str += ` ${fn[i].substring(0, 1)}.`;
      }
      return str;
    },
    openLink(link) {
      window.open(link, "_self");
    },
    ...mapActions(["get_user", "logout", "auth"]),
  },
  computed: {
    ...mapGetters(["user", "isLoading"]),
  },
};
</script>

