import { createStore } from 'vuex'
import { removeEmpty } from './utils/functions';



export default createStore({
    state() {
        return {
            isLoading: false,
            user: {
                person: {
                    fullName: '',
                    lastName: '',
                    firstName: '',
                    middleName: '',
                    files: []
                },
                contacts: []
            }
        }
    },
    mutations: {
        get_user(state, payload) {
            state = {
                ...state, user: {
                    ...state.user,
                    person: {
                        ...state.user.person,
                        ...payload.person
                    },
                    contacts: [
                        ...state.user.contacts,
                        ...payload.contacts
                    ]
                }
            }
        },
        start_loading(state) {
            state.isLoading = true
        },
        stop_loading(state) {
            state.isLoading = false
        }
    },
    actions: {
        async auth() {
            await fetch('/api/auth/esia', {
                method: 'get',
            }).then(res => {
                return res.json()
            }).then(res => {
                // let url = new URL(res.data.value);
                // let search = url.search;
                // search = search.replace(
                //     "http%3A%2F%2Ftest-letters.orb.ru%2Fcontinue",
                //     "http%3A%2F%2F127.0.0.1%3A8080/continue"
                // );
                // window.location = "https://esia.gosuslugi.ru/aas/oauth2/ac" + search;
                window.location = res.data.value; //prod
            })
        },
        async logout() {
            await fetch('/api/auth/logout')
                .then(res => {
                    return res.json()
                })
                .then(res => {
                    window.location = res.data.value
                })
        },
        async auth_continue(_, { code }) {
            await fetch('/api/auth/continue?code=' + code, {
                method: 'get'
            }).then(res => {
                return res.json()
            }).then(res => {
                window.location = res.data.value // prod
                // if (res.data !== null) {
                //     let url = new URL(res.data.value)
                //     let search = url.search
                //     if (search !== '') {
                //         search = search.replace('http%3A%2F%2Ftest-letters.orb.ru%2Fcontinue', 'http%3A%2F%2F127.0.0.1%3A8080/continue')
                //         window.location = 'https://esia.gosuslugi.ru/aas/oauth2/ac' + search
                //     } else {
                //         window.location.reload()
                //     }
                // }
            })
        },
        async get_registration_place({ commit }) {
            commit('start_loading')
            return await fetch('/api/registration_place/list')
                .then(res => {
                    return res.json();
                }).then(r => r).catch(() => {
                    return { data: [] }
                }).finally(() => commit('stop_loading'))
        },
        async get_user({ state, commit }) {
            commit('start_loading')
            await fetch('/api/user/get', {
                method: 'get'
            }).then(res => {
                return res.json()
            }).then(res => {
                state.user = {
                    ...state.user,
                    ...res.data
                }
            }).finally(() => commit('stop_loading'))
        },
        async save({ commit }, { object, files = [] }) {
            commit('start_loading')
            const formData = new FormData();
            if (files.length !== 0) {
                files.map((fileItem) => {
                    formData.append(`file`, fileItem.file);
                });
            }
            formData.append('claim', new Blob([JSON.stringify(object)], {
                type: "application/json"
            }))

            return await fetch(`/api/claim/save`, {
                method: 'post',
                body: formData
            }).then(res => res.json()).catch(() => {
                return {
                    success: false
                }
            }).finally(() => commit('stop_loading'))
        },
        async get_by_id_and_last_name({ commit }, { id, lastName }) {
            commit('start_loading')
            return await fetch(`/api/claim/get_by_id_and_last_name/${id}/${lastName}`, {
                method: 'get'
            }).then(res => {
                return res.json()
            }).then(r => r).catch(err => console.log(err)).finally(() => commit('stop_loading'))
        },
        async get_claims({ commit }, { limit = 20, offset = 0 }) {
            commit('start_loading')
            return await fetch(`/api/claim/receive_sent?limit=${limit}&offset=${offset}`)
                .then(res => res.json())
                .then(res => {
                    if (res.data !== null) {
                        return res.data.list
                    }
                }).finally(() => commit('stop_loading'))
        },
        async send_simple_info(_, { data }) {
            removeEmpty(data)
            const { person } = data
            delete person.email_confirm
            delete person.type
            await fetch('/api/claim/send_simple_info', {
                method: 'post',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    ...person
                })
            })
        }
    },

    getters: {
        user(state) {
            return state.user
        },
        person(_, getters) {
            return getters.user.person
        },
        isLoading(state) {
            return state.isLoading
        }
    }
})