import { createRouter, createWebHistory } from 'vue-router'
import MainScreen from '@/components/screens/MainScreen'
import FormScreen from '@/components/screens/FormScreen'
import ResultScreen from '@/components/screens/ResultScreen'
import PrivacyScreen from '@/components/screens/PrivacyScreen'
import RulesScreen from '@/components/screens/RulesScreen'

const routes = [
  { path: '/', name: 'main', component: MainScreen },
  { path: '/form', component: FormScreen },
  { path: '/result', name: 'result', component: ResultScreen, props: true },
  { path: '/privacy', component: PrivacyScreen },
  { path: '/rules', component: RulesScreen },
  { path: '/:pathMatch(.*)*', redirect: { name: 'main' } },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
